"use client";

import React from "react";
import { Button } from "react-bootstrap";

interface CartItemProps {
  id: number;
  title: string;
  price: number;
  image1: string;
  shortDescription: string;
  fullDescription: string;
  category: string;
  quantity: number;
  onIncrease: () => void;
  onDecrease: () => void;
  onRemove: () => void;
}

const CartItem: React.FC<CartItemProps> = ({
  id,
  title,
  price,
  image1,
  shortDescription,
  fullDescription,
  category,
  quantity,
  onIncrease,
  onDecrease,
  onRemove,
}) => {
  return (
    <div className="bg-[#493628] text-[#fffbe9] rounded-xl p-4 shadow-md mb-4 border border-[#493628]/30">
      <h3 className="text-lg font-semibold mb-1">{title}</h3>
      <p className="text-sm">Price (one): ${price.toFixed(2)}</p>
      <p className="text-sm mb-2">Total: ${(price * quantity).toFixed(2)}</p>

      <div className="flex items-center gap-3 mb-3">
        <Button
          variant="light"
          size="sm"
          onClick={onDecrease}
          disabled={quantity <= 1}
        >
          -
        </Button>
        <span className="text-base font-medium">Qty: {quantity}</span>
        <Button variant="light" size="sm" onClick={onIncrease}>
          +
        </Button>
      </div>

      <Button
        variant="danger"
        size="sm"
        className="rounded-lg"
        onClick={onRemove}
      >
        Remove
      </Button>
    </div>
  );
};

export default CartItem;
